import Vue from 'vue';
import WaterfallFlow from '../../components/waterfall-flow/waterfall-flow.vue';
export default {
	data() {
		return {
			displayCategory: false,
			categoryList: [],
			currentCategory: {},
			categoryName: '',
			commodityList: [],
			scroll_height: 700,
			loading: false,
		}
	},
	components: {
		WaterfallFlow: WaterfallFlow
	},
	mounted() {
		this.loading = true;
		Vue.prototype.$http.get('/categories/get').then(res => {
			console.log(res);
			this.categoryList = res.data.data.categories;
		});
		Vue.prototype.$http.request({
			url: '/commodities/recommend',
			method: 'POST'
		}).then(res => {
			// console.log(res)
			this.commodityList = res.data.data;
			console.log(this.commodityList);
			this.loading = false;
		});
		setTimeout(() => {
			this.getHeight();
		}, 100)
	},
	methods: {
		getHeight() {
			let that = this;
			let height = 0;
			uni.getSystemInfo({
				success(res) {
					that.screen_height = res.windowHeight;
					let otherHeight = 0;
					let query = uni.createSelectorQuery().in(that);
					query.select('#commodities').boundingClientRect(res => {
						that.scroll_height = that.screen_height - res.top;
					}).exec();
				}
			});
		},
		ChangeCategory(index) {
			if (index == -1) {
				this.currentCategory = {};
				this.categoryName = '全部推荐';
				this.hideModal();
			}
			this.currentCategory = this.categoryList[index];
			this.categoryName = this.currentCategory.category_name;
			this.hideModal();
		},
		showModal(e) {
			this.displayCategory = true;
		},
		hideModal(e) {
			this.displayCategory = false
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		}
	}
}
